  margin: 0;
}

button {
  color: #fff;
  cursor: pointer;
  background: #708090;
  border: none;
  border-radius: 3px;
  outline: none;
  padding: 5px 10px;
}

button:active {
  background: #505050;
}

button:disabled {
  color: #f1f1f1;
  background: #ccc;
}

.wrapper {
  text-align: center;
}

.game {
  text-align: left;
  display: inline-block;
}

.controls {
  margin-top: 10px;
}

.delimiter {
  margin: 0 5px;
}

.delimiter:after {
  content: "|";
}

